import React, { createContext, useState, useContext, useEffect } from "react";

//  Context
const LanguageContext = createContext();

//    
const translations = {
  en: {
    title: "ART OF BETTING",
    slogan: "Bet Smart, Win Strategically",
    aboutUs: "About Us",
    freeBets: "FREE BETS",
    freeBetsDesc: "Check our predictions",
    vip: "VIP",
    vipDesc: "Join our profitable community",
    statistics: "STATISTICS",
    statisticsDesc: "Check our tipsters numbers",
    algorithm: "ALGORITHM",
    algorithmDesc: "Predictions from our smart algorithm",
    poker: "POKER",
    pokerDesc: "Poker Tactics That Will Make You a Champion",
    learnMore: "Learn More",
    switchTo: "",
  },
  gr: {
    title: "ART OF BETTING",
    slogan: " ,  ",
    aboutUs: "  ",
    freeBets: "FREE BETS",
    freeBetsDesc: "   ",
    vip: "VIP",
    vipDesc: "     ",
    statistics: "",
    statisticsDesc: "    tipsters ",
    algorithm: "",
    algorithmDesc: "     ",
    poker: "",
    pokerDesc: "      ",
    learnMore: " ",
    switchTo: "English",
  },
};

// Provider Component
export function LanguageProvider({ children }) {
  //    localStorage  default  
  const [language, setLanguage] = useState(() => localStorage.getItem("language") || "en");

  //    localStorage   
  useEffect(() => {
    localStorage.setItem("language", language);
  }, [language]);

  const toggleLanguage = () => {
    setLanguage((prev) => (prev === "en" ? "gr" : "en"));
  };

  return (
    <LanguageContext.Provider value={{ language, toggleLanguage, translations: translations[language] }}>
      {children}
    </LanguageContext.Provider>
  );
}

// Hook     Context
export function useLanguage() {
  return useContext(LanguageContext);
}
